* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.page-header {
  width: 100%;
  height: 40px;
  background-color: #1151c9;
  position: fixed;
}

.container {
  height: 40px;
  margin: 0 auto;
  padding: 0 15px;
  display: flex;
}

.search-form {
  margin: auto;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  padding-top: 50px;
  display: flex;
}

.photo-card {
  width: 300px;
  border-radius: 0 0 4px 4px;
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

img {
  width: 100%;
  height: 200px;
  margin: 0;
  display: block;
}

.load-more {
  color: #fafafa;
  background-color: #1151c9;
  border: rgba(0, 0, 0, 0);
  border-radius: 4%;
  margin: auto;
}

.info {
  justify-content: center;
  gap: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 0;
  display: flex;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.animated {
  background-color: #1151c9;
  border: rgba(0, 0, 0, 0);
  border-radius: 4%;
  animation: pulse 1s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.4);
  }
}

